@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,400;1,200&family=Raleway&family=Russo+One&display=swap');

:root{
    --main-green: #87b5b5
}

*{
    box-sizing: border-box;
}

body{
    margin: 0px;
    padding: 0px;
    background-color: var(--main-green);
}

/* toolbar */

.toolbar{
    display: flex;
    align-items: center;
    background-color: #4aa3a3;
    padding: 0.3rem;
    border: 1px solid #1d2f2f;
    box-shadow: 1px 1px 10px 2px #3a5f5f;
    font-family: Russo One;
    font-size: 1rem;
    color: black;
}

.toolbar .icon{
    margin-left: auto;
}

/* editor */

#editorWrap{
    max-width: 600px;
    margin: 1.125rem auto;
}



#editorWrap textarea{
    width: 100%;
    min-height: 200px;
    margin-bottom: -5px;
    resize: vertical;
    outline: none;
    padding-left: 5px;
    padding-top: 5px;
    font-size: 0.875rem;
    background-color:  #c0d8d8;
    box-shadow: 1px 1px 10px 2px #3a5f5f;
}


/* previewer */

#previewer{
    max-width: 800px;
    margin: 1.25rem auto;
    min-height: 200px;
    overflow-wrap: break-word;
    background-color:  #c0d8d8;
    box-shadow: 1px 1px 10px 2px #3a5f5f;
}

#preview{
    width: 100%;
    margin-bottom: -5px;
    padding-left: 5px;
    padding-top: 5px;
    padding: 0 1rem;
    overflow: auto;
}

#preview img {
    display: block;
    max-width: 90%;
    margin: 2rem auto;
    
}

.hide{
    display: none;
}
